<template>
  <!-- Î™®Î∞îÏùº Î©îÎâ¥ -->
  <div class="mb-div">
    <div class="mb-bg">
      <div class="mb-top">
        <span class="mb-top-txt">Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.</span>
        <a href="#" class="login">LOGIN</a>
        <button class="mb-close"></button>
      </div>
      <div class="mb-wrap">
        <ul class="mb-menu">
          <li v-for="(item, index) in mbMenuData" v-bind:key="index">
            <span class="mb-mainmenu" v-if="item.menuType === 'S'">{{item.mainText}}</span>
            <a :href="item.mainLink" class="mb-mainmenu" v-if="item.menuType === 'A'" v-on:click.prevent v-on:click.stop>{{item.mainText}}</a>
            
            <ul class="mb-submenu" v-if="item.menuType === 'S'">
              <li v-for="(subitem, subindex) in item.subArr" v-bind:key="subindex">
                <a :href="subitem.link" v-on:click.prevent v-on:click.stop>{{subitem.title}}</a>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery';
  import { onUpdated, computed } from 'vue';
  // import { onMounted, ref } from 'vue';
	import { useStore } from 'vuex';

  export default {
    setup() {
      const store = useStore();
      // const mbMenuData = ref([]);
      // mbMenuData.value = store.state.mbMenuData;
      const mbMenuData = computed(()=>store.getters.getmbMenuData) // {} Î¶¨ÌÑ¥ Íº≠ Ïã†Í≤ΩÏì∞Í∏∞
      // ÌôîÏÇ¥Ìëú Ìï®ÏàòÎ°ú Ìï¥Ïïº ÏúÑÏùò storeÏôÄÏùò Î≤îÏúÑÍ∞Ä Í∞ôÏïÑÏßê
      store.dispatch('fetchmbMenuData');

      console.log("mbMenuData", mbMenuData)
// ÌôîÎ©¥Ïóê html Ïùò Íµ¨ÏÑ±Ïù¥ ÏôÑÎ£åÎêòÎ©¥ 
      onUpdated(() => {
        // Î™®Î∞îÏùºÎ©îÎâ¥
        let mb_div = $('.mb-div')
        // Î™®Î∞îÏùº Î≥¥Í∏∞ Î≤ÑÌäº Í∏∞Îä•
        let mb_bt = $('.mb-bt');
        mb_bt.click(function () {
          mb_div.show();
        })
        // Î™®Î∞îÏùº Îã´Í∏∞ Î≤ÑÌäº Í∏∞Îä•
        let mb_close = $('.mb-close');
        mb_close.click(function () {
          mb_div.hide();
        })
        // Î∞∞Í≤ΩÎàÑÎ•¥Î©¥ Îã´Í∏∞
        mb_div.click(function () {
          mb_div.hide();
        })
        // ÎÇ¥Ïö©ÏùÑ ÌÅ¥Î¶≠ÌïòÎ©¥ Î∞∞Í≤ΩÏúºÎ°ú Ïã†Ìò∏ Ï†ÑÎã¨ÏùÑ ÎßâÎäîÎã§.
        $('.mb-bg').click(function (event) {
          // Ïã†Ìò∏ Ï†ÑÎã¨ ÎßâÍ∏∞
          event.stopPropagation();
        });

        // Î™®Î∞îÏùº Î©îÎâ¥ Í∏∞Îä•
        let mb_menu_li = $('.mb-menu > li');
        $.each(mb_menu_li, function (index) {
          // mb-mainmenu Î•º Ï∞æÏïÑÏÑú Î≥¥Í¥Ä
          let temp = $(this).find('.mb-mainmenu');
          temp.click(function () {
            // ÌéºÏ≥êÏ†∏ ÏûàÎäî Í≤ΩÏö∞ true, ÏóÜÏúºÎ©¥ false
            let result = temp.hasClass('mb-mainmenu-open');

            if (result == true) {
              // ÌéºÏ≥êÏßÑ ÌÅ¥ÎûòÏä§ Í∞ÄÏßÑ Í≤ΩÏö∞
              temp.removeClass('mb-mainmenu-open');
              // ÌéºÏ≥êÏßÑ ÏÑúÎ∏åÎ©îÎâ¥ Îã´Í∏∞
              mb_menu_li.find('.mb-submenu').hide();

            } else {
              // Î™®Îì† ÌÅ¥ÎûòÏä§Î•º ÏùºÎã® Ï†úÍ±∞ÌïúÎã§.
              mb_menu_li.find('.mb-mainmenu').removeClass('mb-mainmenu-open');
              // Î™®Îì† ÌéºÏ≥êÏßÑ ÏÑúÎ∏åÎ©îÎ•òÎ•º Îã´ÎäîÎã§.
              mb_menu_li.find('.mb-submenu').hide();

              // ÌéºÏ≥êÏßÑ ÌÅ¥ÎûòÏä§ ÏóÜÎäî Í≤ΩÏö∞
              temp.addClass('mb-mainmenu-open');
              // ÏÑúÎ∏åÎ©îÎâ¥ ÌéºÏπòÍ∏∞
              mb_menu_li.eq(index).find('.mb-submenu').show();

            }
          })
        })
        // window ÎÑàÎπÑ Ï≤¥ÌÅ¨ 
        $(window).resize(function () {
          let temp = $(window).width();
          if (temp >= 880) {
            mb_div.hide();
            $('.mb-mainmenu').removeClass('mb-mainmenu-open');
            $('.mb-submenu').hide();
          }
        });
      });
      return {
          mbMenuData
      }
    }
  }
</script>

<style>
  /*Î™®Î∞îÏùº Î©îÎâ¥*/
  .mb-div {
    position: fixed;
    left: 0;
    top: 0;
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 99999;
  }

  .mb-bg {
    position: relative;
    width: 380px;
    height: 100%;
    padding: 0 30px;
    background-color: #fff;
  }

  .mb-top {
    position: relative;
    display: block;
    width: 320px;
    padding-top: 35px;
    padding-bottom: 20px;
    margin-bottom: 35px;
    border-bottom: 1px solid #dcdcdc;
  }

  .mb-top-txt {
    position: relative;
    display: block;
  }

  .login {
    position: relative;
    display: inline-block;
    font-size: 14px;
    color: #ffcc00;
    margin-top: 15px;
    font-weight: 500;
  }

  .mb-close {
    position: absolute;
    right: 0;
    bottom: 20px;
    display: block;
    width: 20px;
    height: 20px;
    background: url("@/assets/images/btn-close.png") no-repeat center;
    background-size: contain;
    border: 0;
    cursor: pointer;
  }

  .mb-wrap {
    position: relative;
    display: block;
    width: 320px;
  }

  .mb-menu {
    position: relative;
    display: block;
  }

  .mb-menu>li {
    position: relative;
    display: block;
    margin-bottom: 25px;
  }

  .mb-mainmenu {
    position: relative;
    display: block;
    font-size: 16px;
    color: #777;
    font-weight: 500;
    margin-bottom: 20px;
    cursor: pointer;
  }

  .mb-mainmenu::after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 15px;
    height: 15px;
    background: url("@/assets/images/ico-gnb-link-m.png") no-repeat center;
    background-size: contain;
  }

  .mb-mainmenu-open {
    color: #ffcc00;
  }

  .mb-mainmenu-open::after {
    background: url("@/assets/images/ico-gnb-m-active-up.png") no-repeat center;
    background-size: contain;
  }

  .mb-submenu {
    position: relative;
    display: none;
  }

  .mb-submenu li {
    position: relative;
    display: block;
    padding-left: 20px;
    margin-bottom: 16px;
  }

  .mb-submenu li a {
    position: relative;
    display: block;
    font-size: 14px;
  }
</style>